/**
 * Í∏∞Ï°¥ path.basenameÍ≥º Ïú†ÏÇ¨Ìïú Ïó≠Ìï†ÏùÑ ÏàòÌñâ
 *
 * @param filePath ÌïÑÏöîÌïú ÌååÏùº Ï£ºÏÜå
 * @param suffix ÌïÑÏöîÌïú ÌååÏùº Ï£ºÏÜå
 * @return ÌååÏùºÏùò Ïù¥Î¶ÑÏùÑ Í∑úÏπôÏóê ÎßûÍ≤å Î∞òÌôòÌï©ÎãàÎã§
 *
 */
export const getBasename = (filePath: string, suffix?: string) => {
  const basename = filePath.split("/").pop() ?? "";
  if (suffix && basename.endsWith(suffix)) {
    return basename.slice(0, -suffix.length);
  }
  return basename;
};
